.superInput {
  padding: 12px 20px;
  margin: 8px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  outline: none;
  font: 300 20px 'Roboto Mono';
  &__errorInput {
    border-color: #e3291b;
  }

  &__error {
    color: #e3291b;
  }
}

$primary: #11998e;
$secondary: #38ef7d;
$white: #fff;
$gray: #222020;
.form__group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  width: 300px;
}

.form__field {
  font-family: inherit;
  width: 300px;
  border: 0;
  border-bottom: 2px solid $gray;
  outline: 0;
  font-size: 20px;
  color: $white;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;

  &::placeholder {
    color: transparent;
  }

  &:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: $gray;
}

.form__field:focus {
  ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: $primary;
    font-weight: 700;
  }
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, $primary, $secondary);
  border-image-slice: 1;
}
/* reset input */
.form__field {
  &:required,
  &:invalid {
    box-shadow: none;
  }
}
